<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querSearch || '搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div @click="orderAll" class="sort-item" ref="all">综合</div>
      <div @click="orderSales" class="sort-item" ref="sales">销量</div>
      <div @click="orderPrice" class="sort-item" ref="price">价格 </div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getProList } from '@/api/product'

export default {
  name: 'SearchIndex',
  components: {
    GoodsItem
  },
  computed: {
    // 获取地址栏的搜索关键字
    querSearch () {
      return this.$route.query.search
    }
  },
  data () {
    return {
      page: 1,
      proList: [],
      sortType: 'all', // all综合搜索 sales销量搜索 price价格搜索
      sortPrice: 1, // 0低到高 1高到低
      orderName: '' // 排序高亮名
    }
  },
  methods: { // 提供一个方法，调用以处理业务逻辑。
    // 发送数据给接口, 从而得到商品列表数据, 进而渲染
    async render () {
      const { data: { list } } = await getProList({
        categoryId: this.$route.query.categoryId,
        goodsName: this.querSearch,
        page: this.page,
        sortType: this.sortType,
        sortPrice: this.sortPrice
      })
      this.proList = list.data
      this.page = list.current_page
    },

    // 高亮
    highlight (a) {
      if (this.orderName) {
        this.$refs[this.orderName].classList.remove('highlight')
      }
      this.orderName = a
      this.$refs[this.orderName].classList.add('highlight')
    },

    orderAll () {
      this.sortType = 'all'
      this.render()
      this.highlight('all')
    },
    orderSales () {
      this.sortType = 'sales'
      this.render()
      this.highlight('sales')
    },
    orderPrice () {
      this.sortType = 'price'
      if (this.sortPrice) {
        this.sortPrice = 0
      } else {
        this.sortPrice = 1
      }
      this.render()
      this.highlight('price')
    }
  },
  created () {
    this.render()
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}

.highlight {
  color: #e8af35
}
</style>
